.Over {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 50px;
  margin: 0;
}
/* body {
  height: 100vh;
  overflow: hidden;
  display: grid;
  place-items: center;
  background: #ccc;
} */

.single-product-wrap {
  /* box-shadow: 0px 5px 17px -7px rgba(0.1, 0, 0, 0.75); */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
  transition: 0.3s;
}

img.product__img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.status__main {
  position: absolute;
  top: 0%;
  right: 0%;
  display: flex;
  margin-top: 11px;
  left: auto;
  bottom: auto;
}

.single-product-wrap {
  border-radius: 4px;
}

.property__details {
  font-family: system-ui, sans-serif;
  display: flex;
  align-items: baseline;
  text-transform: uppercase;
}

.details__bed__bath {
  text-transform: uppercase;
  font-weight: 700;
  color: #718096;
  margin-left: 10px;
  letter-spacing: 0.1em;
  font-size: 0.75rem;
}

.badge__status {
  /* color: #fff; */
  font-size: 11px;
  font-weight: 700;
  line-height: 11px;
}
.badge__status_update {
  /* color: #fff; */
  font-size: 11px;
  font-weight: bold;
  color: #1e55ad;
  line-height: 11px;
}

.purple {
  background-color: #d6bcfa;
  padding: 1px 4px;
  border-radius: 2px;
  color: #6b46c1;
}

.red {
  background-color: #fed7d7;
  padding: 1px 4px;
  border-radius: 2px;
  color: #c53030;
}

.teal {
  background-color: #81e6d9;
  padding: 1px 4px;
  border-radius: 2px;
  color: #2c7a7b;
}

/* Begin: Listing Type Status */
/* listing status for sale */
.listing__type__sale {
  margin-right: 7px;
  padding: 2px 4px;
  border-radius: 2px;
  background-color: #272931;
  color: #ffffff;
}

/* listing status for rent */
.listing__type__rent {
  margin-right: 7px;
  padding: 2px 4px;
  border-radius: 2px;
  background-color: #37c1f2;
  color: #ffffff;
}
/* End: Listing Type Status */

.property__tilte {
  margin-top: 6px;
}

.viewed {
  margin-left: 4px;
  font-size: 15px;
  color: #4a5568;
}

.price__of__property {
  color: #4a5568;
  font-size: 0.75rem;
}

.property__card__bottom {
  display: flex;
  justify-content: space-between;
}

.property__icons {
  display: flex;
  align-items: center;
}

.icons__box {
  background-color: #edf2f7;
  margin: auto;
  height: 35px;
  width: 35px;
  padding: 5px;
  border-radius: 35px;
  margin-left: 5px;
}
.icons__box:hover {
  background-color: #cbd5e0;
}

.icon {
  bottom: 0;
  top: 0;
  margin-left: 5px;
}
svg:not(:root) {
  margin-bottom: 5px;
}

.joinChatContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.joinChatContainer h3 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.joinChatContainer input {
  width: 210px;
  height: 40px;
  margin: 7px;
  border: 2px solid #43a047;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

.joinChatContainer button {
  width: 225px;
  height: 50px;
  margin: 7px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  background: #43a047;
  color: #fff;
  cursor: pointer;
}

.joinChatContainer button:hover {
  background: #2e7d32;
}

.chat-window {
  width: 50%;
  height: 420px;
  margin-top: 40px;
  margin-left: 24%;
}

.chat-window p {
  margin: 0;
}
.chat-window .chat-header {
  height: 45px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.chat-window .chat-body {
  height: calc(450px - (45px + 70px));
  border: 1px solid #263238;
  background: #fff;

  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  /* min-height: 40px; */
  /* max-width: 50%; */
  background-color: #0069d9;
  border-radius: 5px;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 5px;
  margin-left: 5px;
  padding-right: 5px;
  padding-left: 5px;
  overflow-wrap: break-word;
  word-break: break-word;
}

#you {
  justify-content: flex-start;
}

#you .message-content {
  justify-content: flex-start;
}

#you .message-meta {
  justify-content: flex-start;
  margin-left: 5px;
}

#other {
  justify-content: flex-end;
}

#other .message-content {
  justify-content: flex-end;
  background-color: #0069d9;
}

#other .message-meta {
  justify-content: flex-end;
  margin-right: 5px;
}

.message-meta #author {
  margin-left: 10px;
  font-weight: bold;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #263238;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85% 1;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;

  outline: none;
  font-family: "Open Sans", sans-serif;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15% 1;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  color: lightgray;
}

.chat-window .chat-footer button:hover {
  color: #0069d9;
}
.hide {
  opacity: 0 !important;
}

